                <!--                <td-->
                <!--                    class="subtitle-2"-->
                <!--                    v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"-->
                <!--                >-->
                <!--                  <template>-->
                <!--                    <v-tooltip bottom>-->
                <!--                      <template-->
                <!--                          v-slot:activator="{ on }"-->
                <!--                          v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"-->
                <!--                      >-->
                <!--                        <v-btn-->
                <!--                            @click="editBills(item)"-->
                <!--                            color="secondary"-->
                <!--                            icon-->
                <!--                            text-->
                <!--                            v-on="on"-->
                <!--                            v-show="$vuetify.breakpoint.smAndDown || hover"-->
                <!--                        >-->
                <!--                          <v-icon>mdi-account-edit</v-icon>-->
                <!--                        </v-btn>-->
                <!--                      </template>-->
                <!--                      <span>{{ $vuetify.lang.t("$vuetify.btn.edit") }}</span>-->
                <!--                    </v-tooltip>-->
                <!--                    &lt;!&ndash;<v-tooltip bottom>&ndash;&gt;-->
                <!--                    &lt;!&ndash;<template&ndash;&gt;-->
                <!--                    &lt;!&ndash;v-slot:activator="{ on }"&ndash;&gt;-->
                <!--                    &lt;!&ndash;v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"&ndash;&gt;-->
                <!--                    &lt;!&ndash;&gt;&ndash;&gt;-->
                <!--                    &lt;!&ndash;<v-btn&ndash;&gt;-->
                <!--                    &lt;!&ndash;@click="deleteItem(item)"&ndash;&gt;-->
                <!--                    &lt;!&ndash;color="red"&ndash;&gt;-->
                <!--                    &lt;!&ndash;icon&ndash;&gt;-->
                <!--                    &lt;!&ndash;text&ndash;&gt;-->
                <!--                    &lt;!&ndash;v-on="on"&ndash;&gt;-->
                <!--                    &lt;!&ndash;v-show="$vuetify.breakpoint.smAndDown || hover"&ndash;&gt;-->
                <!--                    &lt;!&ndash;dark&ndash;&gt;-->
                <!--                    &lt;!&ndash;&gt;&ndash;&gt;-->
                <!--                    &lt;!&ndash;<v-icon>mdi-delete-outline</v-icon>&ndash;&gt;-->
                <!--                    &lt;!&ndash;</v-btn>&ndash;&gt;-->
                <!--                    &lt;!&ndash;</template>&ndash;&gt;-->
                <!--                    &lt;!&ndash;<span>{{ $vuetify.lang.t("$vuetify.btn.remove") }}</span>&ndash;&gt;-->
                <!--                    &lt;!&ndash;</v-tooltip>&ndash;&gt;-->
                <!--                  </template>-->
                <!--                </td>-->
                <!--                <td class="subtitle-2" v-else></td>-->
              </tr>
            </tbody>
          </v-hover>
        </template>
      </v-data-table>
      <delete-item
        :dialog="dialogDelete"
        :messages="messagesDelete"
        :urlItems="urlDeleteItems"
